import * as Bluebird from "bluebird";

import SaavnPlatform from "./other/saavn/saavn_platform";
import SongInput from "./input/song_input";

new SaavnPlatform().createSongParser()
    .create(() => new Bluebird<SongInput>(resolve => {
        let input = new SongInput();
        input.url = "https://www.saavn.com/s/song/english/Grateful/Im-the-One/CRkSXw1XT0k";

        resolve(input);
    }))
    .then(parser => parser.parseTitle())
    .then(parser => {
        console.log(JSON.stringify(parser.output, null, 2));
    })
    .catch(error => {
        console.error(error);
    });
